
import HisApp from "@/apps/app_lib"
import { defineAsyncComponent, defineComponent, ref, watch } from 'vue'
import HisDate from "@/utils/Date"
import { Encounter } from "@/interfaces/encounter"
import { Option } from "@/components/Forms/FieldInterface"
import { Patientservice } from "@/services/patient_service"
import { ProgramService } from "@/services/program_service"
import { ObservationService } from "@/services/observation_service"
import { DrugOrderService } from "@/services/drug_order_service"
import { OrderService } from "@/services/order_service"
import TaskSelector from "@/components/DataViews/TaskSelectorModal.vue"
import EncounterView from "@/components/DataViews/DashboardEncounterModal.vue"
import CardDrilldown from "@/components/DataViews/DashboardTableModal.vue"
import { WorkflowService } from "@/services/workflow_service"
import { toastSuccess, toastDanger, toastWarning } from "@/utils/Alerts";
import { findIndex, isArray, isEmpty } from "lodash"
import {
    man,
    sync,
    time,
    person,
    calendar,
    medical,
    woman,
    clipboardOutline, 
    appsOutline, 
    folderOutline,
    logOutOutline, 
    clipboard, 
    apps, 
    folder,
    logOut,
    alertCircle,
    timeOutline, 
    warningOutline 
} from "ionicons/icons";
import {
	IonList,
    IonLabel,
    IonItem,
    IonSegment,
    IonSegmentButton,
    IonPage,
    IonIcon,
    IonChip,
    IonContent,
    IonButton,
    IonGrid,
    IonRow,
    IonCol,
    IonFooter,
    IonToolbar,
    modalController,
    loadingController
} from "@ionic/vue";
import { EncounterService } from '@/services/encounter_service'
import { ConceptService } from "@/services/concept_service"
import { PersonService } from "@/services/person_service"
import { TaskInterface } from "@/apps/interfaces/TaskInterface"
import { resolveWorkflowAction } from "@/utils/WorkflowTaskHelper"
import App from "@/apps/app_lib"
import PrimaryCard from "@/components/DataViews/DashboardPrimaryCard.vue"
import VisitDatesCard from "@/components/DataViews/VisitDatesCard.vue"
import Display from "@/composables/display"
import FullToolbar from "@/components/PatientDashboard/Poc/FullToolbar.vue"
import { PatientDashboardCard, VisitDateParams } from "@/apps/interfaces/AppInterface"
import Store from "@/composables/ApiStore"
import { GeneralDataInterface } from "@/apps/interfaces/AppInterface"
import { Offline, isOfflineMode, isOnline } from "@/composables/offline"
import dayjs from "dayjs"
import { ObservationOffline, SyncData } from "@/interfaces/Sync"

export default defineComponent({
    components: {
        IonList,
        IonLabel,
        IonItem,
        IonSegment,
        IonSegmentButton,
        IonChip,
        IonPage,
        IonIcon,
        IonFooter,
        IonContent,
        IonButton,
        IonToolbar,
        IonGrid,
        IonRow,
        IonCol,
        FullToolbar,
        PrimaryCard,
        VisitDatesCard,
        MinimalToolbar: defineAsyncComponent(() => import("@/components/PatientDashboard/Poc/MinimalToolbar.vue"))
    },
    setup() {
        const screenBreakPoint = ref('lg' as 'lg' | 'sm')
        const { resolution } = Display()
        watch(() => resolution.value, (dimensions) => {
            if (dimensions && dimensions.width <= 900) {
                screenBreakPoint.value = 'sm'
            } else {
                screenBreakPoint.value = 'lg'
            }
        }, { immediate: true, deep: true })
        return {
            sync,
            time,
            timeOutline,
            person,
            calendar,
            medical,
            clipboard, 
            apps, 
            folder,
            logOut, 
            clipboardOutline, 
            appsOutline, 
            folderOutline,
            logOutOutline,
            alertCircle,
            screenBreakPoint
        }
    },
    data: () => ({
        online: isOnline,
        initMode: '' as 'online' | 'offline' | '',
        activeTab: 1 as number,
        app: {} as any,
        dashboardComponent: {} as any,
        isBDE: false as boolean,
        currentDate: '',
        sessionDate: '',
        nextTask: {} as any,
        programId: 0 as any,
        patientId: 0 as number|string,
        patient: {} as any,
        patientProgram: {} as any,
        patientCardInfo: [] as Array<Option>,
        programCardInfo: [] as GeneralDataInterface[] | [],
        visitDates: [] as Array<Option>,
        activeVisitDate: '' as string | number,
        patientCards: [] as PatientDashboardCard[],
        appVersion: ProgramService.getFullVersion(),
        sessionEncounterMap: {} as Record<string, any>,
        savedEncounters: [] as string[]
    }),
    computed: {
        patientIsset(): boolean {
            return !isEmpty(this.patient)
        },
        patientName(): string {
            return !isEmpty(this.patient) 
                ? this.patient.name
                : 'N/A'
        },
        appHasCustomContent(): boolean {
            return !isEmpty(this.app.customPatientDashboardContentComponent)
                ? true
                : false
        },
        customDashboardContent(): any {
            return this.app.customPatientDashboardContentComponent
        },
        visitDatesTitle(): string {
            return `${this.visitDates.length} Visits`
        },
        hasOfflineObs(): boolean {
            const { offlineData } = Offline()
            return offlineData.value.some((sync: SyncData) => {
                return `${sync.id}` === `${this.patientId}` &&
                    (sync.observationParams||[]).some((obs: ObservationOffline) => obs.uploadStatus != 'Success') &&
                    sync.programID === ProgramService.getProgramID()
            })
        }
    },
    watch: {
        online: {
            handler(isOnline) {
                if (isOnline) this.init()
            }
        },
        activeVisitDate(date: string) {
            if (!(this.appHasCustomContent)) this.updateCardVisitData(date)
        }
    },
    created() {
        this.app = App.getActiveApp()
        this.setPatientCards()
    },
    mounted() {
        if (this.$route.params.id) {
            this.patientId = this.$route.params.id as any
            App.doAppManagementTasks().then(() =>{
                // doAppManagementTasks might check for suspended apps and might prompt the user to switch back to the suspended App. 
                // Do a reload here incase that happens
                this.app = App.getActiveApp()
                if (typeof this.app?.overridePatientDashboardCards === 'function') {
                    this.setPatientCards()
                }
                this.programId = this.app.programID
                if (this.appHasCustomContent) {
                    this.patientCards = []
                }
                this.init()
            })
        }
    },
    methods: {
        async initSynchronization() {
            const { synchronizeRecord, findByID } = Offline()
            await this.showLoader("Synchronization has started. this may take a while")
            try {
                const updated = await synchronizeRecord(this.patientId)
                const personID = updated.personID
                if (!personID) {
                    this.clearLoader()
                    return this.patientId
                }
                // patch offline record with PatientID created from the API
                if(`${personID}` != `${this.patientId}`) {
                        const offline = findByID(this.patientId)
                        offline.id = personID
                        offline.personID = personID
                        // Reload the page to use a new NPID from API
                        document.location = `/patient/dashboard/${personID}`
                }
                this.clearLoader()
                return parseInt(`${personID}`)
            } catch(e) {
                console.error("Error synchronizing data " + e)
            }
            this.clearLoader()
        },
        async initOnline() {
            const patientID = parseInt(`${this.patientId}`)
            try {
                let patient = await Store.get('ACTIVE_PATIENT', { patientID })
                if (isEmpty(patient) || this.hasOfflineObs) {
                    // If not found online, try checking offline object
                    const updatedPatientID = await this.initSynchronization()
                    patient = await Store.get('ACTIVE_PATIENT', { 
                        patientID: parseInt(`${updatedPatientID}`)
                    })
                    if (isEmpty(patient)) return false
                }
                this.patient = {
                    patient_id: patient.getID(),
                    birthdate: patient.getBirthdate(),
                    name: patient.getFullName(),
                    currentVillage: patient.getCurrentVillage(),
                    age: patient.getAge(),
                    nationalID: patient.getNationalID(),
                    gender: patient.getGender(),
                    phone: patient.getPhoneNumber()
                }
                Offline().cachePatient(patient)
            } catch(e) {
                console.error(`${e}`)
                toastWarning("Unable to initialise client online")
                return false
            }
            this.patientCardInfo = this.getPatientCardInfo(this.patient)
            this.getNextTask().then((task) => this.nextTask = task)
            this.getPatientVisitDates(patientID)
                .then((dates) => {
                    this.visitDates = dates
                    this.loadSavedEncounters()
                }).catch((e) => {
                    console.error(e)
                })
            this.buildProgramInfoData(patientID)
            isOfflineMode.value = false
            return true
        },
        initOffline() {
            // Search client offline
            const offline = Offline().findByID(this.patientId)
            const person = offline?.personParams?.person
            if (!person)
                return false
            this.patient = {
                patient_id: person.personID,
                birthdate: person.birthdate,
                name: `${person.given_name} ${person.family_name}`,
                currentVillage: person.current_village||'N/A',
                age: person?.age||dayjs(ProgramService.getSessionDate()).diff(person.birthdate, 'years'),
                gender: person.gender,
                phone: person.phone||'N/A',
                nationalID: person.npid||"No npid"
            }
            this.getNextTask().then((task) => this.nextTask = task)
            this.patientCardInfo = this.getPatientCardInfo(this.patient)
            this.buildProgramInfoData(this.patientId)
            return true
        },
        async init() {
            this.currentDate = HisDate.currentDisplayDate()
            this.sessionDate = this.toDate(ProgramService.getSessionDate())
            this.isBDE = ProgramService.isBDE() || false
            this.initMode = ''
            if ((await this.initOnline())) {
                this.initMode = 'online'
            } else if (this.initOffline()) {
                this.initMode = 'offline'
            }
            if (this.initMode) {
                if (this.hasOfflineObs) this.launchSyncDialog()
            } else {
                toastWarning("Unable to initialise dashboard")
            }
        },
        async buildProgramInfoData(patientID: string|number) {
            this.programCardInfo = typeof this.app?.patientProgramInfoData === 'function' 
                ? this.app?.patientProgramInfoData(patientID) 
                : []
            for(const p of this.programCardInfo) {
                try {
                    if (typeof p.init === 'function') {
                        await p.init()
                    }
                    if (typeof p.asyncValue === 'function') {
                        p.asyncValue().then(value => p.value = value)
                    } else if (typeof p.staticValue === 'function') {
                        p.value = p.staticValue()
                    }
                } catch (e) {
                    console.error(`${e}`)
                }
            }
        },
        setPatientCards() {
            const defaultCards: PatientDashboardCard[] = [
                {
                    id: 'c1',
                    items: [],
                    cache: {},
                    label: 'Activities',
                    color: 'primary',
                    isLoading: false,
                    icon: timeOutline,
                    isEnabled: this.isCardEnabled('activitiesEnabled'),
                    onVisitDate: ({card, date, allCards}: VisitDateParams) => {
                        card.isLoading = true
                        const subscribers = allCards.filter(c => c.subscription && c.subscription['c1'])
                        subscribers.forEach(s => s.isLoading = true)
                        EncounterService.getEncounters(parseInt(`${this.patientId}`), {date})
                            .then((encounters) => {
                                this.sessionEncounterMap = {}
                                subscribers.forEach(c => {
                                    if (c.subscription && c.subscription['c1']) {
                                        c.isLoading = false
                                        c.subscription['c1']({encounters, card: c, date})
                                    }
                                })
                                card.items = this.getActivitiesCardInfo(encounters)
                                if (card.cache) card.cache[date] = card.items
                                // Preserve today's encounters (BY SESSION DATE) in a hash object
                                if (date === ProgramService.getSessionDate() && !isEmpty(encounters)) {
                                    this.sessionEncounterMap = encounters.reduce((accum: any, encounter: Encounter) => {
                                        accum[encounter.type.name.toLowerCase()] = encounter.observations
                                            .reduce((concepts: any, obs: any) => concepts.concat(obs.concept.concept_names), [])
                                            .map((concept: any) => concept.name.toLowerCase())
                                        return accum
                                    }, {})
                                }
                                card.isLoading = false
                            }).catch((e) => {
                                console.error(e)
                                card.items = []
                                card.isLoading = false
                                if (card.cache) card.cache[date] = card.items
                                subscribers.forEach(c => c.isLoading = false)
                            })
                    },
                    onClick: (card: any) => {
                        if (!card.isEnabled) 
                            return
                        if (!card.isLoading) {
                            this.openModal(
                                card.items, 'Select Activities', EncounterView
                            )
                        } else {
                            toastWarning('Please wait...')
                        }
                    } 
                },
                {
                    id: 'c2',
                    items: [],
                    cache: {},
                    color: 'success',
                    isLoading: false,
                    label: 'Lab Orders',
                    icon: timeOutline,
                    isEnabled: this.isCardEnabled('labEnabled'),
                    onVisitDate: ({card, date}: any) => {
                        card.isLoading = true
                        this.getLabOrderCardInfo(date)
                            .then((data) => {
                                card.items = data
                                card.cache[date] = card.items
                                card.isLoading = false
                            }).catch(() => {
                                card.items = []
                                card.cache[date] = card.items
                                card.isLoading = false
                            })
                    },
                    onClick: (card: any) => card.isEnabled && this.$router.push(`/art/encounters/lab/${this.patientId}`)
                },
                {
                    id: 'c3',
                    items: [],
                    label: 'Alerts',
                    color: 'danger',
                    icon: warningOutline,
                    isLoading: false,
                    isInit: false,
                    isEnabled: this.isCardEnabled('alertsEnabled'),
                    onVisitDate: ({ card }: any) => {
                        if (card.isInit) return
                        const d  = this.getPatientAlertCardInfo()
                        if(typeof d === 'object' && typeof d.then === 'function') {
                            card.isLoading = true
                            this.getPatientAlertCardInfo()
                                .then((data) => {
                                    if (data) card.items = data
                                    card.isInit = true
                                    card.isLoading = false
                                }).catch(() => {
                                    card.items = []
                                    card.isInit = true
                                    card.isLoading = false
                                }) 
                        }
                        if (d) card.items = d
                    },
                    onClick: () => { /* TODO, list all alerts */ }
                },
                {
                    id: 'c4',
                    items: [],
                    cache: {},
                    label: 'Medications',
                    color: 'warning',
                    icon: timeOutline,
                    isLoading: false,
                    isEnabled: this.isCardEnabled('medicationsEnabled'),
                    onVisitDate: ({card, date }: VisitDateParams) => {
                        card.isLoading = true
                        DrugOrderService.getOrderByPatient(parseInt(`${this.patientId}`), {'start_date': date})
                            .then((medications) => {
                                card.items = this.getMedicationCardInfo(medications)
                                if (card.cache) card.cache[date] = card.items
                                card.isLoading = false
                            }).catch(() => {
                                card.items = []
                                if (card.cache) card.cache[date] = card.items
                                card.isLoading = false
                            })
                    },
                    onClick: (card: any) => {
                        if (!card.isEnabled) 
                            return
                        if (card.isLoading) return toastWarning('Please wait..')
                        const columns = ['Medication', 'Start date', 'End date', 'Amount given']
                        const rows = card.items.map((medication: any) => ([
                            medication.other.drug.name, 
                            this.toDate(medication.other.order.start_date),
                            this.toDate(medication.other.order.auto_expire_date),
                            medication.other.quantity
                        ]))
                        this.openTableModal(columns, rows, 'Medication History')
                    }
                }
            ]
            // Allow Apps to override default cards with their own here
            if (typeof this.app?.overridePatientDashboardCards === 'function') {
                for(const card of this.app.overridePatientDashboardCards()) {
                    const defaultCardIndex = findIndex(defaultCards, {id: card.id})
                    if (defaultCardIndex >= 0) defaultCards[defaultCardIndex] = card
                }
            }
            this.patientCards = defaultCards
        },
        launchSyncDialog() {
            Offline().launchObservationDialog(this.patientId, {
                onDeleteEncounter: () => this.init()
            })
        },
        isCardEnabled(setting: 'activitiesEnabled' | 'alertsEnabled' | 'medicationsEnabled' | 'labEnabled') {
            const conf = this.app?.configDefaultPatientDashboardCards 
            return conf && setting in conf  ? conf[setting] || false : true 
        },
        async showLoader(message="Please wait....") {
            (await loadingController.create({
                message,
                backdropDismiss: false
            })).present()
        },
        clearLoader() {
            loadingController.getTop().then(v => v ? loadingController.dismiss() : null)
        },
        toDate(date: string | Date) {
            return HisDate.toStandardHisDisplayFormat(date)
        },
        toTime(date: string | Date) {
            return HisDate.toStandardHisTimeFormat(date)
        },
        loadSavedEncounters() {
            EncounterService.getSavedEncounters(parseInt(`${this.patientId}`))
                .then((encounters) =>this.savedEncounters = encounters) 
        },
        updateCardVisitData(visitDate: string, invalidateCache=false, voidedEncounter='') {
            this.patientCards.forEach((card) => {
                if (typeof card.cache === 'object' && card.cache[visitDate] && !invalidateCache) {
                    card.items = card.cache[visitDate]
                } else if (card.isEnabled && typeof card.onVisitDate === 'function') {
                    card.onVisitDate({ 
                        card, 
                        invalidateCache,
                        date: visitDate, 
                        allCards: this.patientCards,
                        patient: this.patient
                    })
                } else {
                    card.items = []
                }
            })
        },
        async getPatientVisitDates(patientId: number) {
            return (await Patientservice.getPatientVisits(patientId, false))
                .map((date: string) => ({
                    label: this.toDate(date), 
                    value: date,
                    other: {
                        isActive: date === ProgramService.getSessionDate()
                    }
                }))
        },
        async getNextTask() {
            if (isOfflineMode.value) {
                return Offline().nextPatientWorkflowActivity(this.patientId)||{}
            }
            return WorkflowService.getNextTaskParams(parseInt(`${this.patientId}`))
        },
        goToNextTask() {
            if (this.nextTask?.patientUrl && isOfflineMode.value) {
                return this.$router.push(`${this.nextTask?.patientUrl}/${this.patientId}`)
            }
            resolveWorkflowAction(this.nextTask?.name, this.patientId as any)
        },
        onActiveVisitDate(data: Option) {
            this.activeVisitDate = data.value
        },
        getPatientCardInfo(patient: any) {
            const genderIcon = patient.gender === 'M' ? man : woman
            return [
                { label: "Name", value: patient.name, other: { icon: genderIcon}},
                { label: "Birthdate", value: `
                    ${this.toDate(patient.birthdate)}
                    (${patient.age}) 
                    (${patient.nationalID})`
                },
                { label: "Current Village", value: patient.currentVillage },
                { label: "Phone#", value: patient.phone}
            ]
        },
        getActivitiesCardInfo(encounters: Array<Encounter>) {
           return encounters.map((encounter: Encounter) => {
                return {
                label: `${encounter.type.name}`.toUpperCase(),
                value: this.toTime(encounter.encounter_datetime),
                other: {
                    provider: PersonService.getPersonFullName(encounter.provider_id),
                    id: encounter.encounter_id,
                    columns: ['Observation', 'Value', 'Time'],
                    onVoid: async (reason: any) => {
                        await this.showLoader()
                        EncounterService.voidEncounter(encounter.encounter_id, reason)
                            .then(() => {
                                this.clearLoader()
                                this.loadSavedEncounters()
                                this.updateCardVisitData(this.activeVisitDate as string, true)
                                this.getNextTask().then((task) => this.nextTask = task)
                                toastSuccess('Encounter has been voided!', 2000)
                            }).catch((e) => {
                                this.clearLoader()
                                toastDanger(`${e}`, 32000)
                            })
                    },
                    getRows: async () => {
                        const data = []
                        const { observations } = encounter
                        for(const index in observations) {
                            let concept = '<UNKNOWN CONCEPT>'
                            const obs =  observations[index]
                            try {
                                if (obs?.concept?.concept_names) {
                                    concept = obs.concept.concept_names[0].name
                                } else {
                                    concept = await ConceptService.getConceptName(obs.concept_id)
                                }
                            } catch (e) {
                                console.error(obs, e)
                            }
                            const value = await ObservationService.resolvePrimaryValue(obs)
                            const time = HisDate.toStandardHisTimeFormat(obs.date_created)
                            data.push([concept, value, time])
                        }
                        return data
                    }
                }
                }
            })
        },
        getMedicationCardInfo(medications: any) {
            return medications.map((medication: any) => ({
                label: medication.drug.name,
                value: this.toTime(medication.order.start_date),
                other: medication
            }))
        },
        async getLabOrderCardInfo(date: string) {
            if (typeof this.app.getPatientDashboardLabOrderCardItems === 'function') {
                return this.app.getPatientDashboardLabOrderCardItems(this.patientId, date)
            }
            const labOrders = await OrderService.getOrders(parseInt(`${this.patientId}`), {date})
            return labOrders.map((order: any) => ({
                label: order.specimen.name,
                value: this.toTime(order.order_date)
            }))
        },
        async getPatientAlertCardInfo(){
            if ('getPatientDashboardAlerts' in this.app) {
                return this.app.getPatientDashboardAlerts(this.patient)
            }
        },
        async changeApp() {
            const app = await HisApp.selectApplication('PatientDashboard', true);
            if (!app) return
            if (app.programID != this.programId) {
                this.programId = app.programID
                this.$router.push(`/patients/confirm?patient_barcode=${this.patient.nationalID}`)
            } else {
                this.init()
            }
        },
        /** 
         * Loads all patient program tasks. Perfoms additional checks
         * to ensure that tasks completed on current session date are marked
        */
        async showTasks() {
            if ('primaryPatientActivites' in this.app) {
                const tasks = [...this.app.primaryPatientActivites].map(
                    (task: TaskInterface) => {
                        const taskName = (task.encounterTypeName || task.name).toLowerCase()
                        // check if key concept names from a task are present in encounters
                        // to mark it as completed
                        if (typeof task.taskCompletionChecklist === 'object' && !isEmpty(this.sessionEncounterMap)) {
                            task.taskCompleted = task.taskCompletionChecklist
                                .every(item => isArray(this.sessionEncounterMap[taskName])
                                    && this.sessionEncounterMap[taskName].includes(item.toLowerCase())
                            )
                        } else {
                            // for tasks that dont have key concepts defined, just check presence of 
                            // the encounter itself
                            task.taskCompleted = !isEmpty(this.sessionEncounterMap[taskName])
                        }
                        return task
                    })
                this.openModal(tasks, 'Tasks for', TaskSelector, this.sessionDate)
            }
        },
        showOptions() {
            if ('secondaryPatientActivites' in this.app) {
                const other = this.app.secondaryPatientActivites
                this.openModal(other, 'Select Activity', TaskSelector)
            }
        },
        async startModal(component: any, props: any) {
            (await modalController.create({
                component: component,
                backdropDismiss: false,
                cssClass: "large-modal",
                componentProps: props
            })).present()
        },
        openModal(items: any, title: string, component: any, date='') {
            this.startModal(component, {
                items,
                title: `${title}: ${date || this.toDate(this.activeVisitDate.toString())}`,
                taskParams: { 
                    patient: this.patient,
                    gender: this.patient.gender,
                    program: this.patientProgram,
                    visitDate: this.activeVisitDate,
                    patientID: this.patientId,
                    savedEncounters: this.savedEncounters
                }
            })
        },
        openTableModal(columns: any, rows: any, title: string) {
            this.startModal(CardDrilldown, {
                columns,
                rows,
                title: `${title}: ${this.toDate(this.activeVisitDate.toString())}`
            })
        },
        onCancel() {            
            Store.invalidate('ACTIVE_PATIENT')
            Store.invalidate('PATIENT_PROGRAM')
            Store.invalidate('PATIENT_LAB_ORDERS')
            this.$router.push({path: '/'})
        }
    }
})
